import './App.css';
import {Routes,Route} from 'react-router-dom';
import Header from './Components/Header/Header';
import Footer from './Components/Footer/Footer';
import Home from './Components/Home/Home.js';
import UploadImg from './Components/Upload/UploadImg.js'
import CategoryImg from './Components/Category/CategoryImg.js'
import SearchImg from './Components/search/SearchImg.js'
import { Fragment } from 'react';


function App() {
  return (
    <Fragment>
      <Header/>
    <Routes>
     
      <Route path='/' element={<Home />} />
      <Route path='/upload' element={<UploadImg />} />
      <Route path='/category/:value' element={<CategoryImg />} />
      <Route path='/search/:keyword' element={<SearchImg />} />
       
    </Routes>
    <Footer/>
    </Fragment>
  );
}

export default App;
