import React, { Fragment, useEffect, useState } from "react";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import { CreateImage } from "../../actions/imageAction";
import { CREATE_IMAGES_RESET } from "../../constants/imageConstant";
import "./Uploadimg.css";
import imgL from "../Header/Images/screen.jpg"
import Loading from "../Loader/loading";

const Category = [
  "Background",
  "Education",
  "Religion",
  "Science",
  "Technology",
  "Sports",
  "Nature",
  "Animals",
  "Arts",
  "Healthcare",
  "Foods",
  "Business",
  "People"
];

const UploadImg = () => {
  const dispatch = useDispatch();
  const {isCreate,loading
    ,error} = useSelector(state=>state.createImage)
    

  const [name, setName] = useState("");
  const [category, setCategory] = useState("");
  const [img, setImg] = useState("");
  const [imgPreview, setImgPreview] = useState(imgL);

  const Navigate = useNavigate();
  const alert = useAlert()

  const imgChangeHandler = (e) => {
    const reader = new FileReader();

    reader.onload = () => {
      if (reader.readyState === 2) {
        setImgPreview(reader.result);
        setImg(reader.result);
      }
    };

    reader.readAsDataURL(e.target.files[0]);
  };

  const formSubmitHandler = (e) => {
    e.preventDefault();

    const myForm = new FormData();

    myForm.set("name", name);
    myForm.set("category", category);
    myForm.set("img", img);

    console.log(img);
    dispatch(CreateImage(myForm));
  };
useEffect(() => {
  if(error){
    alert.error(error);
  }
  if(isCreate){
    alert.success("Image Created Successfully")
    Navigate("/");
    dispatch({type:CREATE_IMAGES_RESET})

  }
  
}, [error,dispatch,alert,Navigate,isCreate]);

  return (
      <Fragment>
        {
            loading ? <Loading/> : (
              <div className="uploadContainer">
            
     
          <div>
            <h3>CREATE IMAGE</h3>
          </div>
  
          <div className="uploadBox">
            <div>
              <form onSubmit={formSubmitHandler}>
                <input
                  type="file"
                  name="img"
                  accept="image/*"
                  onChange={imgChangeHandler}
                />
                max 1mb file upload
                <input
                  type="text"
                  placeholder="Name of your image or type"
                  name="name"
                  value={name}
                  required
                  onChange={(e) => setName(e.target.value)}
                />
                <select
                  value={category}
                  onChange={(e) => setCategory(e.target.value)}
                >
                  <option value=""> Choose Role</option>
                  {
                    Category && Category.map((item)=>(
                      <option value={item}> {item}</option>
                    ))
                  }
                </select>
                <button type="submit" className="C-btn">CREATE</button>
              </form>
            </div>
  
            <div>
              <img src ={imgPreview}  alt="Preview" />
            </div>
          </div>
        </div>
        )
      }
      </Fragment>
    
  );
};

export default UploadImg;
