import React, { Fragment } from "react";
import { Link } from "react-router-dom";
import "./Header.css";
// import imag from './Images'
import MenuIcon from '@material-ui/icons/Menu'



const Header = () => {
  const CategoryHoverBox = document.getElementsByClassName("CategoryHoverBox");
  const headerMenuBox = document.getElementsByClassName("headerMenuBox");

  function funOver() {
    CategoryHoverBox[0].classList.remove("hide");
  }
  function funOut() {
    CategoryHoverBox[0].classList.add("hide");
  }
  function funOverMenu() {
    headerMenuBox[0].classList.remove("hide2");
  }
  function funOutMenu() {
    headerMenuBox[0].classList.add("hide2");
  }

  // let categoryDiv = [
  //   {
  //         image: 
  //           "./Images/Animals.png",
  //         Category: "Background",
  //       },
  // ]

  let categoryDiv = [
    {
      image: 
        "./Images/screen.jpg",
      Category: "Background",
    },
    {
      image:
        "./Images/Sports.png",
      Category: "Sports",
    },
    {
      image: 
        "./Images/education.png",
      Category: "Education",
    },
    {
      image: "./Images/Religion.png",
      Category: "Religion",
    },
    { image: "./Images/Science.png",
      Category: "Science",
    },
    { image: "./Images/Technology.png",
      Category: "Technology",
    },
    {
      image: "./Images/People.png",
      Category: "People",
    },
    { image: "./Images/screen.jpg",
      Category: "Arts",
    },
    { image: "./Images/Animals.png",
      Category: "Animals",
    },
    { image: "./Images/screen.jpg",
      Category: "Nature",
    },
    { image: "./Images/Healthcare.png",
      Category: "Healthcare",
    },
    { image: "./Images/screen.jpg",
      Category: "Foods",
    },
    { image: "./Images/Business.png",
      Category: "Business",
    },
  ];

  return (
    <Fragment className="main">
      <div className="headerContainer">
        <span
          className="menuIcon "
          onMouseOver={funOverMenu}
          onMouseOut={funOutMenu}
        > <MenuIcon/> </span>
        <div className="headerLogo">
          <img
            src={require("./Images/logoImageWorld.png")}
            alt=""
          />
        </div>
        <div
          className="headerMenuBox hide2"
          onMouseOver={funOverMenu}
          onMouseOut={funOutMenu}
        >
          <Link to="/" className="headerMenu">
            {" "}
            <div>HOME</div>
          </Link>
          <Link to="/about" className="headerMenu">
            {" "}
            <div>ABOUT</div>
          </Link>
          <Link to="/contect" className="headerMenu">
            {" "}
            <div>CONTECT US</div>
          </Link>
          <div className="headerMenu" onMouseOver={funOver} onMouseOut={funOut}>
            CATEGORYS
          </div>
          <Link to="/upload" className="headerMenu">
            <div className="btnn">UPLOAD</div>
          </Link>
        </div>
      </div>

      <div
        className="CategoryHoverBox hide"
        onMouseOver={funOver}
        onMouseOut={funOut}
      >
        <div className="Point"></div>

        <div className="categoryMainDiv">
          {categoryDiv &&
            categoryDiv.map((item) => (
              <Link to={`/category/${item.Category}`}>
                <div className="categoryDiv">
                  <img src={require(`${item.image}`)} alt="" />
                  {/* <img src={require("./Images/download.jfif")} alt="dd" /> */}
                  <p>{item.Category}</p>
                </div>
              </Link>
            ))}
        </div>
      </div>

      <div className="categorysContainer">
        {categoryDiv &&
          categoryDiv.map((item) => (
            <Link to={`/category/${item.Category}`}>
              <div className="categoryBox categoryBoxColor1">
                {item.Category}
              </div>
            </Link>
          ))}
      </div>
    </Fragment>
  );
};

export default Header;
