import {ALL_IMAGES_FAIL,
ALL_IMAGES_REQUEST,
ALL_IMAGES_SUCCESS,
CATEGORY_IMAGES_FAIL,
CATEGORY_IMAGES_REQUEST,
CATEGORY_IMAGES_SUCCESS,
CLEAR_ERROR, 
CREATE_IMAGES_FAIL, 
CREATE_IMAGES_REQUEST, 
CREATE_IMAGES_SUCCESS, 
SEARCH_IMAGES_FAIL, 
SEARCH_IMAGES_REQUEST,
SEARCH_IMAGES_SUCCESS,
CREATE_IMAGES_RESET,
UPDATE_IMAGES_REQUEST,
UPDATE_IMAGES_SUCCESS,
UPDATE_IMAGES_FAIL} from '../constants/imageConstant'


export const ImagesReducers = ( state = { images: [] }, action ) => {

    switch (action.type) {
        case ALL_IMAGES_REQUEST:
            return{
                loading:true,
                images:[]
            }
        case ALL_IMAGES_SUCCESS:
            return{
                loading:false,
                images:action.payload.images,

            }   

        case ALL_IMAGES_FAIL:
            return{
                loading:false,
                error:action.payload
                
            }
        case CLEAR_ERROR:
            return{
                ...state,
                error:null
            }
    
        default:
            return state;
    }
};

