export const CreateReducers = ( state = {}, action ) => {

    switch (action.type) {
        case CREATE_IMAGES_REQUEST:
            return{
                loading:true,
                isCreate:false
            }
        case CREATE_IMAGES_SUCCESS:
            return{
                loading:false,
                isCreate:action.payload
            }   

        case CREATE_IMAGES_FAIL:
            return{
                loading:false,
                error:action.payload
                
            }
            case CREATE_IMAGES_RESET:
                return{
                    loading:false,
                    isCreate:false
                    
                }    
        case CLEAR_ERROR:
            return{
                ...state,
                error:null
            }
    
        default:
            return state;
    }
};

