import React, { useEffect, useState } from "react";
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router";
import { clearError, getAllImages } from "../../actions/imageAction";
import "./Home.css";
import ImagesBox from "./ImagesBox";

const Home = () => {
  const dispatch = useDispatch();
  const { images, error } = useSelector((state) => state.images);
  const alert = useAlert();
  const [search, setSearch] = useState("");

  const Navigate = useNavigate();

  const submitHandler = () => {
    if (search) {
      Navigate(`/search/${search}`);
    }
  };

  useEffect(() => {
    if (error) {
      alert.error(error);
      dispatch(clearError());
    }
    // console.log(error);

    dispatch(getAllImages());
  }, [error, dispatch, alert]);

  return (
    <div className="MainContainer">
      <div className="BannerContainer">
        <img
          src={require("../Header/Images/logoImageWorld.png")}
          alt="LOGO"
        />

        <div>
          <input
            type="text"
            required
            placeholder="Search"
            onChange={(e) => setSearch(e.target.value)}
            value={search}
          />
          <button onClick={submitHandler}>Find</button>
        </div>
        <p>-----LIFE EASY TO FIND IMAGES-----</p>
      </div>

      <div className="ImagesHeading">
        <h2>WELCOLE TO IMAGES WORLD</h2>
      </div>

      <div className="ImagesContainer1">
        {images && images.map((item) => <ImagesBox item={item} />)}
      </div>
    </div>
  );
};

export default Home;
