import axios from 'axios'
import {ALL_IMAGES_FAIL,
ALL_IMAGES_REQUEST,
ALL_IMAGES_SUCCESS, 
CATEGORY_IMAGES_FAIL, 
CATEGORY_IMAGES_REQUEST, 
CATEGORY_IMAGES_SUCCESS, 
CLEAR_ERROR,
CREATE_IMAGES_FAIL,
CREATE_IMAGES_REQUEST,
CREATE_IMAGES_SUCCESS,
SEARCH_IMAGES_FAIL,
SEARCH_IMAGES_REQUEST,
SEARCH_IMAGES_SUCCESS,
UPDATE_IMAGES_FAIL,
UPDATE_IMAGES_REQUEST,
UPDATE_IMAGES_SUCCESS} from '../constants/imageConstant'

export const getAllImages = ()=> async (dispatch)=>{

    try {
        dispatch({type:ALL_IMAGES_REQUEST});

        const { data } = await axios.get("/api/w2/images");

        dispatch({
            type:ALL_IMAGES_SUCCESS,
            payload: data
        })
        
        
    } catch (error) {
        dispatch({
            type: ALL_IMAGES_FAIL,
            payload: error.response.data.message,
          });
        
    }

}
export const getCategoryImages = (value)=> async (dispatch)=>{
    
    try {
        dispatch({type:CATEGORY_IMAGES_REQUEST});
        
        const { data } = await axios.get(`/api/w2/category?value=${value}`);
        // console.log(data)

        dispatch({
            type:CATEGORY_IMAGES_SUCCESS,
            payload: data
        })
        
    } catch (error) {
        dispatch({
            type: CATEGORY_IMAGES_FAIL,
            payload: error.response.data.message,
          });
        
    }

}
export const findImages = (keyword)=> async (dispatch)=>{
    
    try {
        dispatch({type:SEARCH_IMAGES_REQUEST});
        
        const { data } = await axios.get(`/api/w2/filter/images/?keyword=${keyword}`);
        console.log("data")

        dispatch({
            type:SEARCH_IMAGES_SUCCESS,
            payload: data
        })
        
    } catch (error) {
        dispatch({
            type: SEARCH_IMAGES_FAIL,
            payload: error.response.data.message,
          });
        
    }

}
export const UpdateImage = (id,type)=> async (dispatch)=>{
    
    // console.log("updateImage Action ",id)
    try {
        dispatch({type:UPDATE_IMAGES_REQUEST});
        const config = { headers: { "Content-Type": "application/json" } };
        
        
        const { data } = await axios.put(`/api/w2/update/${id}`,{type},config);
        
        dispatch({
            type:UPDATE_IMAGES_SUCCESS,
            payload:data.success
        })
        
        
    } catch (error) {
        dispatch({
            type: UPDATE_IMAGES_FAIL,
            payload: error.response.data.message,
          });
        
    }

}
export const CreateImage = (useData)=> async (dispatch)=>{
    
    // console.log("CreateImage Action ", useData)
    try {
        dispatch({type:CREATE_IMAGES_REQUEST});
        const config = { headers: { "Content-Type": "application/json" } };
        
        
        const { data } = await axios.post("/api/w2/create", useData,config);
        
        dispatch({
            type:CREATE_IMAGES_SUCCESS,
            payload:data.success
        })
        
        
    } catch (error) {
        dispatch({
            type: CREATE_IMAGES_FAIL,
            payload: error.response.data.message,
          });
        
    }

}

// clearing error
export const clearError = () => async (dispatch) => {
    dispatch({ type: CLEAR_ERROR });
  };