import React from "react";
import DownloadIcon from "@material-ui/icons/CloudDownload";
import LikeIcon from "@material-ui/icons/Favorite";
import "./ImagesBox.css";
import { useDispatch, useSelector } from "react-redux";
import { UpdateImage } from "../../actions/imageAction";
import { useEffect } from "react";
import { useAlert } from "react-alert";
import { saveAs } from "file-saver";



const ImagesBox = ({ item }) => {
  const dispatch = useDispatch();
  const { isUpdated, error } = useSelector((state) => state.updateImg);
  const alert = useAlert();

  const likeCountHandler = (type) => {
    dispatch(UpdateImage(item._id, type));
  };
const downloadLink = (imgPath,fileName)=>{

  saveAs(imgPath, fileName)
}
  useEffect(() => {
    if (error) {
      alert.error(error);
    }
    if (isUpdated) {
      // alert.error(error);
    }
  }, [isUpdated, alert, error]);

  // console.log(item.image.url);
  return (
    <div className="ImageBoxContainer">
      <img src={item.image.url} alt={item.Name} />
      <div>
        <p>{item.name}</p>
        <div>
         
          <div
            onClick={() => {
              likeCountHandler("download");
              downloadLink(item.image.url,item.name );
            }}
          >
            <DownloadIcon />
            {item.downloadCount}
          </div>
        
          <div onClick={() => likeCountHandler("like")}>
            <LikeIcon />
            {item.likeCount}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ImagesBox;
