import {createStore, combineReducers, applyMiddleware} from 'redux';
import thunk from 'redux-thunk';
import {composeWithDevTools} from 'redux-devtools-extension';

import { CategoryReducers, CreateReducers, FindReducers, ImagesReducers, UpdateReducers } from './reducers/imageReducer'


const reducers = combineReducers({
    images:ImagesReducers,
    cate_images: CategoryReducers,
    find_images:FindReducers,
    createImage:CreateReducers,
    updateImg:UpdateReducers

});

const middleware = [thunk];


const store = createStore(
    reducers,
    composeWithDevTools( applyMiddleware(...middleware))
)

export default store;