import React, { useEffect } from 'react'
import { useAlert } from "react-alert";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from 'react-router'
import { getCategoryImages } from '../../actions/imageAction';
import ImagesBox from '../Home/ImagesBox';

const CategoryImg = () => {
  const dispatch = useDispatch();
  const {images, error,loading} = useSelector(state=>state.cate_images);
  const alert = useAlert();

    let {value} =useParams();
    // let value= "wallpaper";


    useEffect(() => {
      dispatch(getCategoryImages(value));
      
    }, [dispatch,value]);

    
  return (
    <div>
      <div className="ImagesHeading">
        <h2>WELCOLE TO IMAGES WORLD</h2>
      </div>

      <div className="ImagesContainer1">
        {
          images && images.map((item)=>(
            <ImagesBox item={item} />
          ))
        }
      </div>
    </div>
  )
}

export default CategoryImg