import React, { useEffect } from 'react'
import { useAlert } from 'react-alert';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router'
import { clearError, findImages } from '../../actions/imageAction';
import ImagesBox from '../Home/ImagesBox';

const SearchImg = () => {
  const alert = useAlert();
  const dispatch = useDispatch();
  const { images, error, loading } = useSelector(state=>state.find_images);

const { keyword }=  useParams();
useEffect(() => {
if(error){
  alert.error(error)
  dispatch(clearError());
}


  dispatch(findImages(keyword));
  
}, [dispatch,error,keyword]);

  return (
    <div>
       <div>
      <div className="ImagesHeading">
        <h2>WELCOLE TO IMAGES WORLD</h2>
      </div>

      <div className="ImagesContainer1">
        {
          images && images.map((item)=>(
            <ImagesBox item={item} />
          ))
        }
      </div>
    </div>
    </div>
  )
}

export default SearchImg